.ex-box {
	padding: 5px 10px 5px 10px;
	border: 4px solid black;
	border-radius: 5px;
	margin: 25px 5px 25px 5px;
}

.title-box {
	color: #fff;
	margin: 5px;
	padding: 5px 10px 5px 10px;
}


.game-img-box {
	display: inline;
}

.game-img {
	border: 5px solid black;
	border-radius: 100%;
	background: #fff;
	margin: 5px;
	cursor: pointer;
}

.game-img:hover {
	animation: rotate-image 2s -0.5s linear infinite;
}

@keyframes rotate-image {
  0% {transform: rotateZ(0deg) scaleX(1) scaleY(1);}
  50% {transform: rotateZ(0deg) scaleX(0.95) scaleY(0.85);}
  100% {transform: rotateZ(0deg) scaleX(1) scaleY(1);}
}