.App {
  background-color: black;
  position: fixed;
  height: 100%;
  width: 100%;
  font-family: "Times New Roman", "Times", serif;
}

.contentW {
  background-image: url('../public/img/background.jpeg');
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
  background-color: black;
  position: fixed;
  width: 142vh;
  height: 100%;
}

.contentH {
  background-image: url('../public/img/background.jpeg');
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
  background-color: black;
  position: fixed;
  width: 100%;
  height: 70.5vw;
}

.box:hover {
  color: black!important;
}

.image:hover {
    border-radius: 30%;
    background: linear-gradient(235deg, #89ff00, #060c21, #00bcd4);
    background-size: 200%;
    box-shadow: inset 0 5px 0 rgba(0, 250, 250, 0.6), inset 5px 0 0 rgba(0, 200, 200, 0.6), inset 0 -5px 0 rgba(0, 150, 200, 0.6), inset -5px 0 0 rgba(0, 200, 250, 0.6);
    animation: animate 30s linear infinite, rotate-before 2s -0.5s linear infinite;
}

.imageEmpty:hover {
    border-radius: 30%;
    padding: 10px;
    background-size: 200%;
    box-shadow: inset 0 5px 0 rgba(0, 250, 250, 0.6), inset 5px 0 0 rgba(0, 200, 200, 0.6), inset 0 -5px 0 rgba(0, 150, 200, 0.6), inset -5px 0 0 rgba(0, 200, 250, 0.6);
    animation: animate 30s linear infinite, rotate-before 2s -0.5s linear infinite;
}

.menu {
  position: absolute;
  color: white;
  cursor: pointer;
}

.video-responsive {
  overflow: hidden;
  padding-bottom: 56.25%;
  position: relative;
  height: 0;
}

.video-common iframe {
  left: 0;
  top: 0;
  position: absolute;
}

.video-power iframe {
  height: 100%;
  width: 95%;
}

.video-main iframe {
  height: 100%;
  width: 100%;
}

.menu:hover::after { 
 content: attr(data-title); 
 font-family: "Times New Roman", "Times", serif; /* Гарнитура шрифта */
 font-size: 11px; /* Размер текста подсказки */
}

.title {
  font-size: 10vw;
}

#card::-webkit-scrollbar {
  width: 10px;
  background-color: #f9f9fd;
}

#card::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #18aaaa;
}

#card::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.2);
  border-radius: 10px;
  background-color: #f9f9fd;
}

@keyframes rotate-before {
  0% {transform: rotateZ(0deg) scaleX(1) scaleY(1);}
  50% {transform: rotateZ(0deg) scaleX(0.95) scaleY(0.85);}
  100% {transform: rotateZ(0deg) scaleX(1) scaleY(1);}
}

@keyframes animate {
  0%,
  100% { background-position: 0 0; }
  50% { background-position: 100% 0; }
}

.games-text {
  text-align: center;
  margin: 0;
  font: bold 100px/1 "Times New Roman", "Times", serif;
  color: #fff;
  text-shadow: 0 1px 0 #cccccc, 0 1px 0 #c9c9c9, 0 1px 0 #bbbbbb, 0 1px 0 #b9b9b9, 0 1px 0 #aaaaaa, 0 1px 1px rgb(0 0 0 / 10%), 0 0 3px rgb(0 0 0 / 10%), 0 1px 3px rgb(0 0 0 / 7%), 0 3px 1px rgb(0 0 0 / 10%), 0 1px 1px rgb(0 0 0 / 7%), 0 1px 1px rgb(0 0 0 / 7%), 0 6px 4px rgb(0 0 0 / 15%);
  -webkit-transition: .2s all linear;           
}

.title-text {
  text-align: center;
  margin: 0;
  font: bold 100px/1 "Times New Roman", "Times", serif;
  color: #fff;
  text-shadow: 0 1px 0 #cccccc, 0 1px 0 #c9c9c9, 0 1px 0 #bbbbbb, 0 1px 0 #b9b9b9, 0 1px 0 #aaaaaa, 0 1px 1px rgb(0 0 0 / 10%), 0 0 3px rgb(0 0 0 / 10%), 0 1px 3px rgb(0 0 0 / 7%), 0 3px 1px rgb(0 0 0 / 10%), 0 1px 1px rgb(0 0 0 / 7%), 0 1px 1px rgb(0 0 0 / 7%), 0 6px 4px rgb(0 0 0 / 15%);
  -webkit-transition: .2s all linear;     
  text-transform: uppercase;    
  font-size: 3.2vw; 
  margin-bottom: 10px; 
}

.description {
  color: #fff;
  text-indent: 1.5em;
}

.audio:hover {
  color: #fff!important;
  animation: rotate-before 2s -0.5s linear infinite;
}