.grid-container {
  display: grid;
  grid-template-columns: auto auto auto;
  text-align: center;
}

.box1 {
  grid-column-start: 1;
  grid-row-start: 1;
  cursor: pointer;
}

.box2 {
  grid-column-start: 1;
  grid-row-start: 2;
  cursor: pointer;
  text-align: center;
}

.box3 {
  grid-column-start: 1;
  grid-row-start: 3;
}

.box4 {
  grid-column-start: 2;
  grid-row-start: 1;
  grid-row-end: 3;
}

.box5 {
  grid-column-start: 3;
  grid-row-start: 1;
  cursor: pointer;
}

.box6 {
  grid-column-start: 3;
  grid-row-start: 2;
  cursor: pointer;
  text-align: center;
}

.box7 {
  grid-column-start: 3;
  grid-row-start: 3;
  cursor: pointer;
}

.icon:hover {
  border-radius: 15%;
  background-color: black;
  opacity: 50%;
}

#audio-icon:hover {
  border-radius: 15%;
  background-color: black;
  opacity: 80%;
}

#book-icon:hover {
  border-radius: 15%;
  background-color: black;
  opacity: 80%;
}